//                                                      Template de base: 1024px
// -----------------------------------------------------------------------------

$susy: (
  columns: 12,
  gutters: .25,
  gutter-position: inside,
);
@include layout($susy);


// desactivation des éléments du template desktop ------------------------------

iframe,
a#corner_button,
a#corner_button_cart,
#slideshow,
.mc_contenu,
.size-full,
#vosavis,
.display0,
#chat-application {
  display: none;
}

#chat-application {
  margin-bottom: 0;
}

iframe#sb-player {
  display: block;
  iframe,
  #main2
   {
    display: none;
  }
}

.modal-wrapper {
  z-index: 9999;
}

// extends ---------------------------------------------------------------------

%buttonBase{
  background-image: none;
  background-color: $cyan;
  background-repeat: no-repeat;
  font-family: $font-global;
  vertical-align: top;
  width: 32%;
  height: 40px;
  margin-right: .5%;
  border-radius: 3px;
  border:1px solid darken($cyan, 7%);
  color: #fff;
  font-size: .8rem;
  font-weight: normal;
  text-transform: uppercase;
  cursor: pointer;
  box-shadow: 2px 2px 2px 0px rgba(0,0,0,0.3);
  &.active,
  &:hover {
    background-image: none;
    background-color: $magenta;
    border:1px solid darken($magenta, 7%);
    transition: .2s;
  }
}

%buttonBaseAfter{
  color: $white;
  text-transform: uppercase;
  padding: 10px 0;
}

////////////////////////////////////////////////////////////////////////////////
//                                LAYOUT
////////////////////////////////////////////////////////////////////////////////

html { box-sizing: border-box; }
* { box-sizing: inherit; }
@include border-box-sizing;

body {
  width: 100%;
  margin: 0 auto;
  font-family: $font-global;
  background-color: $white;
  background-image: url('../images/bottombg.png') bottom left repeat-x;

  color: $text;
  font-size: 1em;
  line-height: 1.2em;
  font-size: .9rem;
  //overflow-x: hidden;
}

#main {
  width: auto;
  margin: 0 auto;
}

.izoneLeft,
.izoneRight {
  display: none;
}
////////////////////////////////////////////////////////////////////////////////
//                             HEADER
////////////////////////////////////////////////////////////////////////////////

header {
  @include container (100%);
  width: 100%;
  height: 295px;
  position: relative;
  margin: 0 auto;
  text-align: left;
  z-index: 100;
  background-image: url(../images/desktop.png);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: contain;
}



#content {
  position: relative;
  width: 100%;
  margin: -5px auto 0;
  background: none;
  top: 0;
}

#content_bg_top {
  @include container (100%);
  width: 100%;

  height: auto;
  padding: 0 5%;

}

.storycontent {

  //display: flex;
  //flex-direction: row;
  //flex-wrap: wrap;
}

////////////////////////////////////////////////////////////////////////////////
//                        DECLARATIONS GENERALES
////////////////////////////////////////////////////////////////////////////////

// séparateurs -----------------------------------------------------------------

.clear {
  clear: both;
}

hr {
  clear: both;
  position: relative;
  float: none;
  display: block;
  width: 10px;
  margin: 3px auto;
  height: 1px;
  background: transparent;
  border: none;
  line-height: 1px;

  &.contact {
    position: relative;
    float: none;
    display: inline;
    width: auto;
    margin: 3px 0 11px;
    height: 1px;
    background: $text;
    border: none;
    line-height: 1px;
  }

  &.bottom_hr {
    display: none;
  }
}

hr.imgSep {
  height: 5px;
  margin: 10px 0;
  width: 100%;
}


// alignements -----------------------------------------------------------------

.alignright,
.floatRight {
  float: right;
}

.alignleft,,
.floatLeft {
  float: left;
}

.aligncenter {
  margin: 0 auto;
  text-align: center;
}

.floatLeftMg {
  float: left;
  margin-right: 10px;
  vertical-align: top;
}

.textleft {
  float: left;
}

.textright {
  text-align: right;
}

.textcenter {
  text-align: center;
}

.textjustify {
  text-align: justify;
}

// titres ----------------------------------------------------------------------

h1, h2, h3 {
  font-size: 1.1rem;
  line-height: 1.4rem;
  margin: 5px 0;
  font-weight: bold;
  color: $text;
  font-family: $font-global;

  &.categorytitle {
    margin: 0;
  }
}

.h1product {
  display: none;
}

h1 .fa-lock {
  color: $jaune;
}
h1 .fa-shopping-cart {
  color: $magenta;
}

h2{
  color: $cyan;
  padding-bottom: 2px;
  font-size: 1.4rem;
}

h3{
  font-size: 1rem;
}

h4 {
  color: $jaune;
  margin: 20px auto 10px;
  padding: 0;
  font-size: 1rem;
  font-weight: bold;
  font-family: $font-global;
  line-height: 1rem;
  text-transform: uppercase;
  text-align:left;
}
h4.keepTxtColor {
  color: $text;
  margin-bottom: 0;
}

h5 {
  font-family: $font-global;
  text-transform: uppercase;
  font-size: .8rem;
  padding: 0;
  margin: 0;
}

h6 {
  font-family: $font-global;
}


// texte et paragraphes --------------------------------------------------------

p {
  margin-top: 0;

  &.sujet {
    margin: 10px 0;
    width: auto;
  }
}

small {
  font-size: 0.8rem;
  line-height: 1rem;
}

.uppercase {
  text-transform: uppercase;
}

span.pink, .pink_link {
  font-weight: bold;
  color: $magenta;
}

span.colblue {
  color: $cyan;
}

span.subtitle {
  font-size: 11px;
}

span.reg_info {
  font-size: .75rem;
  color: $cyan;
}

.highlight {
  color: $alert;
  font-weight: 900;
}
a .highlight{
  color: $alert;
  font-weight: 900;
  text-decoration: none;
}

.highlight-alt1 {
  color: $cyan;
}
.highlight.small {
  color: $white;
}
.green {
  color: $green;
}
b {
  font-weight: 700;
  b{
    font-weight: 700;
  }
}

.bolder {
  font-weight: 900;
  letter-spacing: .2px;
}

// pour gérer les blocs de texte et les retours à la ligne
.split,
.splitorhide{
  display: inline-block;
}

// images ----------------------------------------------------------------------

img {
  border: 0;
}

// divers ----------------------------------------------------------------------

dd {
  margin-left: 10px;
}

.maj{
  font-weight: 900;
}

.star-note{
  display: block;
}

// éléments cachés -------------------------------------------------------------

.hide {
  visibility: hidden;
}

// pour désactiver un élément sur mobile
.disMob0 {
  display: none;
}

// formulaires -----------------------------------------------------------------

form select {
  height: 40px;
  border: 1px solid $border;
  border-radius: 3px;
  padding-left: 10px;
  color: $text;
  font-size: .8rem;
}

input[type="submit"]{
  @include buttonLog($cyan, 150px);
  height: 40px;
  margin-top: 9px;
}

input:focus {
  border: 1px solid $cyan-bloc;
}

// liens -----------------------------------------------------------------------

a {
  &:link {
    color: $cyan;
    font-weight: bold;
    text-decoration: none;
  }

  &:visited {
    color: $text;
    text-decoration: none;
  }

  &:hover {
    color: $color_5;
  }

  &.invisible_link {
    &:link {
      color: $white;
    }

    &:visited {
      color: $white;
    }

    &:hover {
      color: $color_5;
    }
  }

  &.pink_link {
    &:link {
      color: $magenta;
      text-transform: none;
    }

    &:visited {
      color: $magenta;
      text-transform: none;
    }

    &:hover {
      color: $color_5;
    }
  }

  &.white {
    color: $white;
  }

  &.conditio {
    &:link {
      color: $magenta;
    }

    &:visited {
      color: $magenta;
    }

    &:hover {
      color: $color_5;
    }
  }

  &.lcomment {
    &:hover,
    &:link,
    &:visited {
      width: 22px;
      height: 22px;
      position: absolute;
      left: 200px;
      top: 0;
      background: url('../images/lastcomment.png') no-repeat;
    }
  }

  &.lastcommentlink {
    &:link,
    &:visited {
      position: relative;
      margin-top: 10px;
      float: right;
      display: inline;
    }
  }

  &.pytajnik {
    position: absolute;
    right: 25px;
    top: 16px;
    width: 15px;
    height: 15px;
    background: url("../images/question.png") no-repeat;
    text-decoration: none;
  }

  &:active {
    outline: none;
  }
}


// blocs de mise en page colonne -----------------------------------------------

.bloc-left {
