  width: 49%;
  margin-right:2%;
  float: left;
  text-align: justify;
  ul li {
    margin-bottom: 10px;
  }
}
.bloc-right {
  position: relative;
  width: 49%;
  float: right;
  text-align: justify;
  ul li {
    margin-bottom: 10px;
  }
  img {
    max-width: 100%;
    width: 100%;
