}

.labelIcons {
  margin-bottom: 15px;
  text-align: right;
}

.encart, .encartAlert, .encartFlottant, .card {
  position: relative;
  z-index: 10;
  background: $text;
  padding: .5rem 1.5rem;
  margin: 20px 0;
  color: lighten($text,35%);
  text-align: justify;
