}

.cards {
  display: flex;
  display: -webkit-flex;
  flex-wrap: wrap;
  justify-content: space-between;


  .card {
    position: relative;
    width: 33%;
    margin-top: .5rem;
    margin-bottom: .5rem ;
    padding: 1rem;
    text-align: center;
    h5 {
      margin-top: 10px;
      font-size: 1rem;
      text-align: center;
    }
    p {
      color: $jaune;
      font-weight: normal;
      padding-top: 10px;
      padding-bottom: 0;
      text-align: center;
    }
    a {
      margin: 0 auto;
