  padding-top: 12px;
  width: 150px;
  float: right;
  height: 38px;
  vertical-align: middle;
  text-align: center;
  text-transform: uppercase;
  font-size: .8rem;
  line-height: .8rem;
  color: $white;

  &:hover {
    background: $cyan;
    transition: .2s;
    color: $white;
  }
