// menu génénéral --------------------------------------------------------------

#responsive-menu-container li.responsive-menu-item a{
  text-transform: uppercase;
}

ul {
  padding-left: 0;
  margin-top: 0;
  margin-left: auto;
  margin-right: auto;
  list-style-type: none;

  &#menu_top {
    width:75%;
    position: absolute;
    //width: 708px;
    height: 40px;
    top: 0;
    left: 0;
    text-transform: uppercase;
    font-weight: bold;
    font-size: 1rem ;
