      margin-top: 10px;
      margin-left: 45px;
      height: 1px;
      width: 95%;
      background: $border;
    }
    li {
      position: relative;
      float: none;
      display: inline-block;
      height: 40px;
      margin: 0 auto;
      z-index: 1;
      text-align: center;
      vertical-align: middle;
