.menu_hover {
	position: relative;
	background: #f6f6f6;
	border: 1px solid #e9e9e9;
  margin-top: 10px;
  transition: .2s;
  z-index: 800;
  box-shadow: 3px 3px 2px 0px rgba(0,0,0,0.2);
}
.menu_hover:after, .menu_hover:before {
	bottom: 100%;
	left: 37%;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.menu_hover:after {
	border-color: rgba(246, 246, 246, 0);
	border-bottom-color: #f6f6f6;
	border-width: 15px;
	margin-left: -15px;
}
.menu_hover:before {
	border-color: rgba(233, 233, 233, 0);
	border-bottom-color: #e9e9e9;
	border-width: 16px;
