  background-repeat: no-repeat;
  cursor: pointer;
  //animation: pulse 1.25s 1s cubic-bezier(0.66, 0, 0, 1);
  &:hover{
    //animation-play-state: paused;
    transform: scale(1.2);
  }
}

@keyframes pulse {to {box-shadow: 0 0 0 20px rgba(232, 76, 61, 0);}}

.helpText {
  position: absolute;
  left: 100%;
  top: -10px;
  padding-left: 10px;
  width: 300px;
  color: darken($text, 10%);
  padding: .8rem;
  z-index: 999;
  text-align: left;
  line-height: 1rem;
  font-size: .75rem;
	background: #f6f6f6;
	border: 1px solid #e9e9e9;
  box-shadow: 3px 3px 6px 0px rgba(0,0,0,0.2);
  font-weight: normal;
  min-height: 100px;
  .closeB {
    @include close($text, 1rem, 3px, 6px);
  }
  a {
    color: inherit;
    text-decoration: underline;
    &:hover {
      color: lighten($text, 20%);
    }
  }
  b {
    font-size: .75rem;
    text-transform: uppercase;
    display: inline-block;
    border-radius: 7px;
    margin-right: 1px;
  }
}

.helpText:after, .helpText:before {
	right: 100%;
	top: 28px;
	border: solid transparent;
	content: " ";
	height: 0;
	width: 0;
	position: absolute;
	pointer-events: none;
}

.helpText:after {
	border-color: rgba(246, 246, 246, 0);
	border-right-color: #f6f6f6;
	border-width: 10px;
	margin-top: -10px;
}

.helpText:before {
	border-color: rgba(233, 233, 233, 0);
	border-right-color: #e9e9e9;
	border-width: 11px;
	margin-top: -11px;
}

