  filter: alpha(opacity=0);
  height: 40px;
  direction: ltr;
  cursor: pointer;
}

.fileupload-buttonbar .ui-button,
.fileupload-buttonbar .toggle {
  float: 0;
  margin-bottom: 10px;
  margin-left: 0;
