/* The switch - the box around the slider */
.switch {
 position: relative;
 display: inline-block;
 float: right;
 width: 40px;
 height: 22px;
 margin-left: 5px;
 margin-top: -2px;
 vertical-align: baseline;
}

//-----------------------------------------------------------------confirm modal
.confirmChanges{
  padding: 2rem;
  background: $white;
}

//----------------------------------------------------------------- radio switch
/* Hide default HTML checkbox */
.switch input {display:none;}

/* The slider */
.slider {
 position: absolute;
 cursor: pointer;
 top: 0;
 left: 0;
 right: 0;
 bottom: 0;
 background-color: #ccc;
 -webkit-transition: .4s;
 transition: .4s;
}

.slider:before {
 position: absolute;
 content: "";
 height: 18px;
 width: 18px;
 left: 2px;
 bottom: 2px;
 background-color: white;
 -webkit-transition: .4s;
 transition: .4s;
}

input:checked + .slider {
 background-color: $cyan;
}

input:focus + .slider {
 box-shadow: 0 0 1px $cyan;
}

input:checked + .slider:before {
 -webkit-transform: translateX(18px);
 -ms-transform: translateX(18px);
 transform: translateX(18px);
}

/* Rounded sliders */
.slider.round {
 border-radius: 34px;
}

.slider.round:before {
 border-radius: 50%;
}
