  width: 100%;
  height: auto;
  z-index: 1;
}

// délai effet ombre sous le slide

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    visibility: visible;
    opacity: 1;
  }
}

// animation des éléments sélectionnés dans la preview produit (activée dans chaque page produit via le jotform.js correspondant)
// !! les anims sont les mêmes mais elles ont un nom différent pour pouvoir les alterner sans quoi l'effet ne s'applique q'au premier changement)

@keyframes anim {
  0% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes anim1 {
  0% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes anim2 {
  0% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes anim3 {
  0% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes anim4 {
  0% {
    transform: translateX(60%);
  }
  100% {
    transform: translateX(0);
  }
}

