////////////////////////////////////////////////////////////////////////////////
//                                POP UP / MODALS
////////////////////////////////////////////////////////////////////////////////

.close-modal {
    display: none;
}

.modal-wrapper {
    display: none; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 99999; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

.modal-wrapper.show {
    display: block;
    transition: .8s;
}

.modal {
    display: none;
    position: relative;
    background-color: #fefefe;
    margin: 3% auto; /* 15% from the top and centered */
    padding: 2rem;
    border: 0;
    /*border-radius: 10px;*/
    width: 80%; /* Could be more or less, depending on screen size */
    max-width: 1060px;
    text-align: center;
    transition: all 2s ease;
    opacity: 0;
    box-shadow: 0 4px 4px 4px rgba(0,0,0,0.20);

    .toggle-button {
      display: none;
    }
}

.modal.show {
    display: block;
    opacity: 1;
    animation: scale .5s 1;
}

.modal .close-modal {
    @include close($text, 60px, 0, 20px);
}
.modal .close-modal:hover {
  color: #ea2a6a;
}

.btn-fermer {
  @include buttonLog($magenta, 150px);
  margin: 15px 0 10px 0;
  padding-top: .5rem;
  &:hover {
    background-color: $cyan;
    border:1px solid darken($cyan, 7%);
  }
  i {
    font-size: 1.5rem;
    margin-left: 5px;
    padding: 0;
    vertical-align: middle;
    line-height: .9rem;
  }
}

.modal.show .close-modal {
    display: block;
}

.modal h1 {
    margin: 0;
    color: $text;
    padding: 2rem;
    font-size: 1.5rem;
    line-height: 1.8rem;
    font-style: normal;
    font-weight: 900;
    text-transform: uppercase;
}

.modal p {
  text-align: justify;
}
/*#modal-ready {
  max-height: 700px;
  overflow-y: scroll;

}*/
.modal .entry-content p {
    font-size: 1rem;
}
.modal .loading {
    width: 100px;
    margin-top: 20px;
}

@keyframes scale {
  0% {
    top: 0;
    transform: scale(0);
  }
  100% {
    top: 0;
    transform: scale(1);
  }
}

.mfp-content {
  animation: scale .3s 1;
}
